@import "fonts";
@import "vars";

* {
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #333;
}

.race {
    max-height: 850px;
    height: 100vh;
    margin: 0 auto;
    position: relative;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    max-width: 640px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Montserrat;
    min-height: 568px;
}

.start-display {
    position: absolute;
    top: 0px;
    max-width: 300px;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    background-color: #0770BB;
    padding: 10px 10px;
    color: #fff;
    display: none;
}


.not-active {
    .start-display {
        display: block;
    }
}


.move-left,
.move-right {
    content: "";
    top: 0;
    position: absolute;
    width: 50%;
    height: 100%;
}

.move-left {
    left: 0;
}

.move-right {
    right: 0;
}

.play-btn,
.restart-btn {
    background-color: transparent;
    border: 2px solid $pale-white;
    outline: none;
    font-family: "Montserrat";
    text-transform: uppercase;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 5px 15px;
    color: $pale-white;
    background-color: $blue;
    font-size: 18px;
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
    }
}

.play-btn {
    top: calc(50% - 38px);
}

.restart-btn {
    top: calc(50% + 19px);
    visibility: hidden;

    &.show {
        visibility: visible;
    }
}
